.tl-timeline-wrapper {
  position: relative;
}
.tl-timeline-wrapper .tl-timeline-reset-buttons {
  position: absolute;
  top: -25px;
  display: inline-block;
  left: 50%;
  font-size: 12px;
}

.tl-timelineviewport {
  border-top: none;
  overflow-x: auto;
  overflow-y: hidden;
  display: inline-block;
  width: 1200px;
  flex-grow: 1;
}

.tl-viewport-pseudo-border {
  width: 1px;
  background: #ccc;
}

.tl-timeline {
  position: relative;
}
.tl-timeline .tl-cursor {
  position: absolute;
  top: 0;
  left: 0px;
  border-left: 1px dashed #999;
  width: 1px;
  height: 1000px;
  display: none;
  z-index: 10;
  overflow: visible;
  pointer-events: none;
}
.tl-timeline .tl-cursor > div {
  position: absolute;
  background: #999;
  color: #fff;
  padding: 1px;
  font-size: 9px;
  white-space: nowrap;
  width: 75px;
  top: 0px;
  left: -37.5px;
  text-align: center;
  opacity: 1;
  border-radius: 4px;
  display: none;
}
.tl-timeline .tl-track {
  pointer-events: all;
}
.tl-timeline .tl-track line.tl-axis-grid-line.tl-track-highlight {
  stroke: #aaa;
  stroke-width: 1;
  stroke-dasharray: 5 2;
  opacity: 0;
}
.tl-timeline .tl-track rect.tl-track-highlight {
  fill: #f2f2f2;
  opacity: 0;
}

.tl-timeline-leftbar .tl-timeline-tracklabels {
  overflow: hidden;
  display: flex;
  flex-direction: column;
  font-size: 12px;
}
.tl-timeline-leftbar .tl-timeline-tracklabels > div {
  white-space: nowrap;
  border-bottom: 1px dashed #eee;
  padding-right: 10px;
  padding-top: 2px;
}

.tl-zoom-selectbox {
  position: absolute;
  height: 1000px;
  background: #ccc;
  left: 100px;
  z-index: 100;
  opacity: 0.3;
  display: none;
}

.tl-zoom-selectbox-mask {
  position: absolute;
  z-index: 99;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: none;
}

.tl-timeline-tooltip {
  max-width: 480px !important;
  min-width: 300px;
}
.tl-timeline-tooltip:hover {
  z-index: 1070 !important;
}
.tl-timeline-tooltip .popover-content {
  padding: 2px 5px !important;
  border: none;
  font-size: 10px;
}
.tl-timeline-tooltip .popover-content table {
  vertical-align: top;
}
.tl-timeline-tooltip .popover-content table tr:nth-child(even) {
  background: #eeeeee;
}
.tl-timeline-tooltip .popover-content td:first-child {
  font-weight: bold;
}
.tl-timeline-tooltip .popover-content td,
.tl-timeline-tooltip .popover-content th {
  padding: 1px 5px;
}

.timeline-label:last-of-type {
  display: none;
}

.tl-displaynone {
  display: none;
}

.tl-hover-disabled {
  background: none !important;
}
.tl-hover-disabled .tl-track-highlight {
  display: none;
}

.tl-custom-track-header {
  position: relative;
}

.tl-tracklegend {
  z-index: 1000;
  background: #f2f2f2;
  border-radius: 4px 0 0 0;
  transform: translateY(-100%);
  right: 33px;
  padding: 10px 15px;
  font-size: 12px;
}
.tl-tracklegend.hidden {
  display: none !important;
  visibility: hidden;
  height: 0px;
  width: 0px;
}
.tl-tracklegend table td {
  padding: 0 5px 0 0;
}
.tl-tracklegend table tr:last-child td {
  padding-bottom: 0;
}

.popover.cbioTooltip {
  position: absolute !important;
  display: block !important;
  max-width: 600px !important;
  border-radius: var(--border-radius-base); /* Replace with a valid CSS variable or value */
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
}

.popover.cbioTooltip div:last-child {
  white-space: normal;
}

.popover.cbioTooltip p {
  /* margin-bottom: 0 !important; */
}

.popover.cbioTooltip .popover-content,
.popover.cbioTooltip p,
.popover.cbioTooltip td {
  font-size: 11px !important;
}